  width: 80%;
  flex-wrap: wrap;
    @include retina {
      justify-content: flex-end;
    }
}
a.project-grid {
  box-shadow: none;
  display: flex;
  flex-direction: column;
  width: 80%;
  height: 70%;
  padding: 1rem;
  position: relative;
  background: #f4f8f9;
  box-shadow: 0 3px 2px -2px rgba(0,0,0,.25);
  align-items: center;
  margin: 1rem;
  justify-content: space-between;
    img {
      display: block;
      width: 70%;
      position: relative;
      z-index: 1;
      filter: grayscale(100%);
      opacity: 0.5;
    }
      @include retina {
      width: 100%;
      height: auto;
    }
    @include respond-to ('medium') {
      width: 16em;
    }
    h2 {
        font-family: 'Overpass Mono';
        font-weight: 100;        
        text-align: center;      
      @include retina {
        font-size: 3rem;
      }
    }
}
.project-grid .overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(45deg,$bright-blue,rgba(255,255,126,.9));
  opacity: 0;
  transition: opacity .5s;
  z-index: 2;
  color: #fff;

        .content {
          transform: translateY(-47%);
          position: absolute;
          top: 50%;
          transition: transform .5s .1s;
          padding: 40px;
          width: 100%;
          opacity: 0;

        }

}
.project-grid:hover > .overlay {
    opacity: 1;
}
.project-grid:hover > .overlay .content {
  transform: translateY(-50%);
opacity: 1
}

